.filtros {
  list-style: none;
  display: flex;
  margin: 20px auto;
  justify-content: center;
  font-size: 24px;
  color: rgb(153, 84, 158);
}

.filtros li {
  margin: 20px;
  cursor: pointer;
  font-weight: bold;
}

.filtros--loading {
  opacity: 25%;
}

.filtros li:hover,
.active {
  color: green;
}
